<template>
  <div>
    <!-- <div class="block">房间信息</div> -->
    <mt-button class="time" type="primary" size="small" @click.native="change">时间段选择</mt-button>
    {{ this.shijianduan }}
    </br>
    <mt-button class="picker" @click="openPicker" type="primary" size="small">日期选择</mt-button>
    {{ this.formatTime }}
    <mt-datetime-picker ref="picker" v-model="pickerValue" type="date" year-format="{value} 年" month-format="{value} 月" date-format="{value} 日" :startDate="startDate" @confirm="handleConfirm">
    </mt-datetime-picker>
    <ul class="fangjian">
      <li v-for="fangjian in fangjians">
        <span class="roomInfo">
          <img class="picture" :src="fangjian.picture">
          <p class="room">房间{{ fangjian.id + 1 }}</p>
          <p class="numbers">{{ fangjian.numbers }}/12</p>
        </span>
      </li>
    </ul>
  </div>
</template>
<script type="text/javascript">
import { DatetimePicker } from 'mint-ui'

export default {
  name: 'fangjian',
  data() {
    return {
      shijianduan: '13:00-16:00',
      fangjians: [],
      fangjians0: [],
      fangjians1: [],
      pickerValue: new Date(),
      startDate: new Date(),
    }
  },
  computed: {
    formatTime() {
      return `${this.pickerValue.getFullYear()}-${this.pickerValue.getMonth() + 1}-${this.pickerValue.getDate()}`
    }
  },
  created() {
    this.fangjians = this.fangjians0 = [{
      id: 0,
      numbers: 11,
      picture: 'http://123.206.218.163/picture/4.jpg'
    }, {
      id: 1,
      numbers: 11,
      picture: 'http://123.206.218.163/picture/2.jpg'
    }, {
      id: 2,
      numbers: 11,
      picture: 'http://123.206.218.163/picture/3.jpg'
    }, ]
    this.fangjians1 = [{
      id: 0,
      numbers: 11,
      picture: 'http://123.206.218.163/picture/4.jpg'
    }, {
      id: 1,
      numbers: 12,
      picture: 'http://123.206.218.163/picture/2.jpg'
    }, {
      id: 2,
      numbers: 11,
      picture: 'http://123.206.218.163/picture/3.jpg'
    }, ]
    // document.title="房间"
  },
  methods: {
    change() {
      if (this.shijianduan == '13:00-16:00') {
        this.shijianduan = '16:00-19:00'
      } else if (this.shijianduan == '16:00-19:00') {
        this.shijianduan = '19:00-22:00'
      } else if(this.shijianduan == '19:00-22:00'){
        this.shijianduan = '13:00-16:00'
      }
    },
    openPicker() {
      this.$refs.picker.open()
    },
    handleConfirm() {
      // console.log(typeof(this.formatTime))
    }
  }
}

</script>
<style type="text/css">
* {
  margin: 0;
  padding: 0
}

.block {
  width: 100%;
  height: 35px;
  background-color: #50BFFF;
  text-align: center;
  /*color: #5da9ff;*/
  font-size: 25px;
  /*position: fixed;*/
}

.chang {
  margin: 20px;
}

ul {
  list-style: none;
  margin-top: 10px;
  /*text-align: center;*/
  font-size: 15px;
}

.fangjian li {
  /*height: 40px;*/
  /*line-height: 40px;*/
  margin: 10px 15px;
  /*border-bottom: 1px solid #708090;*/
  border-radius: 10px;
  background-color: white;
}

.picture {
  height: 80px;
  widows: 80px;
  line-height: 80px;
  border-radius: 25px;
  padding: 5%;
  float: left;
  position: relative;
}

.roomInfo {
  display: inline-block;
}

.room {
  padding: 15px 0 0 120px;
  font-size: 20px;
}

.numbers {
  padding: 10% 5% 0 200px;
  color: #9C9C9C;
}

.picker {
  margin: 10px 20px 0 80px;
}

.time {
  margin: 10px 20px 0 60px;
}

</style>



